  margin-bottom: 10px;
  margin-top: 20px;
}
.horizontal-menu .bar-inner > ul > li > .mega .sub-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
}
.horizontal-menu .bar-inner > ul > li > .mega .sub-menu > li {
  padding-left: 0;
  padding-bottom: 5px;
}
.horizontal-menu .bar-inner > ul > li > .mega .sub-menu > li > a {
  color: #1b1e24;
}
